import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/pages/home/Home.vue'
import ShoppingCart from '@/pages/shopping_cart/ShoppingCart.vue'
import Sort from '@/pages/sort/Sort.vue'
import My from '@/pages/my/My.vue'



Vue.use(VueRouter)

const routes = [
  {
    path: '/Home',
    name: 'Home',
    component: Home
  },
  {
    path: '/Sort',
    name: 'Sort',
    component: Sort
  },
  {
    path: '/ShoppingCart',
    name: 'ShoppingCart',
    component: ShoppingCart
  },
  {
    path: '/My',
    name: 'My',
    component: My
  }
]

const router = new VueRouter({
  routes
})

export default router
