<template>
  <div class="home">
    <van-search placeholder="请输入搜索关键词" disabled input-align="center" class="van-hairline--bottom home_search" />
    <van-swipe class="home_swipe" v-if="banner.length > 0" :autoplay="3000" indicator-color="white">
      <van-swipe-item v-for="item in banner" :key="item.id">
        <van-image height="100%" fit="cover" :src="item.img.url" />
      </van-swipe-item>
    </van-swipe>
    <van-grid v-if="floor.length > 0" :border="false" :column-num="floor.length / 2" class="floor">
      <van-grid-item v-for="item in floor" class="floor_item" :key="item.id">
        <van-image :src="item.img" width="0.4rem" radius="100%" />
        <span v-text="item.name" class="text"></span>
      </van-grid-item>
    </van-grid>
    <van-nav-bar title="为你推荐" :border="false" class="list_title"></van-nav-bar>
    <van-list v-model="loading" :finished="finished" finished-text="我已经到底了" @load="onLoad">
      <van-grid class="home_goods" :border="false" :column-num="2">
        <van-grid-item default v-for="item in list" :key="item">
          <div class="home_goods_item">
            <van-image
              fit="cover"
              width="100%"
              height="1.8rem"
              src="https://pop.nosdn.127.net/90d9a7d24dcf4b06a2c5a041c8e6ee9b_800_800.jpg?imageView&thumbnail=345x345&quality=95&type=webp"
            />
            <div
              class="text van-multi-ellipsis--l2"
            >Adidas阿迪达斯Superstar经典款贝壳头男女运动板鞋小白鞋钻闪F33889 金标C77124/C77154升级版FU7712/EG4958</div>
            <div class="current">
              <span>319</span>
            </div>
          </div>
        </van-grid-item>
      </van-grid>
    </van-list>
  </div>
</template>

<script>
export default {
  name: "home",
  data() {
    return {
      banner: [],
      floor: [],
      list: [],
      loading: false,
      finished: false
    };
  },
  created() {
    const headers = this.SHA1.get();
    let url = "https://d.apicloud.com/mcm/api/floor";
    this.axios
      .get(url, {
        params: {
          limit: 10
        },
        headers: headers
      })
      .then(res => {
        this.floor = res.data;
      });
    this.axios
      .get("https://d.apicloud.com/mcm/api/banner", {
        params: {
          limit: 10
        },
        headers: headers
      })
      .then(res => {
        this.banner = res.data;
      });
  },
  methods: {
    onLoad() {
      var self = this;
      setTimeout(function() {
        for (var i = 0; i < 10; i++) {
          self.list.push(self.list.length + 1);
        }

        // 加载状态结束
        self.loading = false;

        // 数据全部加载完成
        if (self.list.length >= 40) {
          self.finished = true;
        }
      }, 1000);
    }
  }
};
</script>

<style scoped lang="stylus">
.home {
  background-color: #fff;
  padding-top 0.64rem
  padding-bottom: 0.5rem;
}
.home_search{
  position fixed
  top 0
  left 0
  right 0
  margin 0 auto
  z-index 1;
}
.home_swipe {
  border-radius: 0.05rem;
  overflow: hidden;
  margin: 0 0.1rem;
  height: 2rem;
  background-color: #fff;
}

.home_goods_item, .home_goods_img, .van-grid-item__content {
  background-color: transparent;
}

.home_goods {
  padding: 0 0.05rem;

  /deep/.van-grid-item__content {
    padding: 0;
  }

  .home_goods_item {
    height: 2.5rem;
    border-radius: 0.05rem;
    background-color: #fff;
    margin: 0.05rem 0.05rem 0;
    overflow: hidden;
    border: 1px solid #ececec;

    .text {
      font-size: 0.14rem;
      margin: 0.05rem;
    }

    .current {
      color: #f00;
      font-size: 0.16rem;
      margin: 0.05rem;

      &:before {
        content: '¥';
        font-size: 0.12rem;
      }

      span {
        font-weight: bold;
        margin-left: 0.05rem;
      }
    }
  }
}

.floor {
  flex-wrap: wrap;
  background-color: #fff;

  .floor_item {
    .text {
      color: #666;
      margin-top: 0.05rem;
      font-size: 0.1rem;
    }
  }
}
</style>