<template>
  <div class="sort">
    <van-nav-bar title="分类" :fixed="true" />
    <van-sidebar v-model="active" class="sort_sidebar scroller_y">
      <van-sidebar-item v-for="(item,index) in list" :title="item.name" :key="index" />
    </van-sidebar>
    <transition name="van-fade" v-for="(item,index) in list" :key="index">
      <van-grid class="sort_list" v-show="index == active" :column-num="3" :border="false">
        <van-grid-item
          v-for="(item1,index) in item.list"
          :text="item1.name"
          :icon="item1.img"
          :key="index"
        />
      </van-grid>
    </transition>
  </div>
</template>

<script>
export default {
  name: "sort",
  data() {
    return {
      list: [],
      active: 0
    };
  },
  computed: {
    sortList() {
      return this.list[this.active].list;
    }
  },
  mounted() {
    const headers = this.SHA1.get();
    this.axios
      .get("https://d.apicloud.com/mcm/api/sort", {
        params: {
          limit: 10
        },
        headers: headers
      })
      .then(res => {
        this.list = res.data;
      });
  }
};
</script>

