import Vue from 'vue'
import App from './App.vue'
import router from './router'
import { Tabbar, TabbarItem, Swipe, SwipeItem, Grid, GridItem, Image, List, NavBar, Col, Row, Icon, Search, Button, Sidebar, SidebarItem} from 'vant'
import axios from 'axios'
import SHA1 from './js/SHA1.js'

Vue.config.productionTip = false

/*添加到原型链*/
Vue.prototype.SHA1 = SHA1
Vue.prototype.axios = axios

Vue.use(Tabbar).use(TabbarItem).use(Swipe).use(SwipeItem).use(Grid).use(GridItem).use(Image).use(List).use(NavBar).use(Col).use(Row).use(Icon).use(Search).use(Button).use(Sidebar).use(SidebarItem)


new Vue({
  router,
  render: h => h(App)
}).$mount('#app')

